.hero-img {
        width: 100%;
        height: 60vh;
        position: relative;
        filter: brightness(0.5);
        background-size: cover;
        background-repeat: no-repeat;
}

.heading {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        align-items: center;
        position: absolute;
        top: 25%;
}

.hero-img h1 {
        font-size: 2.4rem;
}

.hero-img p {
        font-size: 1.4rem;
        text-align: center;
}

@media screen and (max-width:640px) {
        .hero-img h1 {
                font-size: 2rem;
        }
}