.project-heading {
        text-align: center;
        padding: 4rem 0 2rem 0;
}

.project-container {
        max-width: 1140px;
        margin: auto;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 40px;
        padding-bottom: 5rem;
}

.project-card {
        background: #1a1919;
        padding: 1.2rem 1rem;
}

.project-card img {
        width: 100%;
        height: 200px;
}

.project-title {
        color: white;
        padding: 1rem 0;
        text-align: center;
}

.project-details p {
        padding-bottom: 1rem;
        font-size: 1.1rem;
        text-align: center;
}

.project-buttons {
        display: flex;
        justify-content: space-evenly;
        padding: 0.5rem 0;
}

.project-buttons .btn {
        padding: 0.5rem 1rem;
}

@media screen and (max-width:740px) {
        .project-container {
                margin: auto;
                max-width: 90%;
                grid-template-columns: 1fr;
        }
}